
import { Link } from 'react-router-dom';

import { ResumeSteps } from "../component/containers/ResumeSteps";


import dummyUserImg from '../assets/images/backgrounds/dummy-user.png'

//CSS
import '../assets/css/pages/create-resume.css'

function PersonalInformation() {
    return (
        <div className="main-content">
            <section className="personal-information">
                <div className="container">
                    <div className="section-title varient-two">
                        <h4>Create Resume</h4>
                    </div>
                    <ResumeSteps completedSteps={1} />

                    <div className="create-resume-step-content light-blue-bg">
                        <div className="resume-header">
                            <Link to="/" className='back-btn'><i className="bi bi-arrow-left"></i></Link>
                            <h4>Personal Information</h4>
                        </div>
                        <div className="resume-body">
                            <div className='create-resume-form'>
                                <div className='text-center'>
                                    <div className="upload-applicant-img">
                                        <div className='applicant-img'>
                                            <img src={dummyUserImg} alt="" />
                                        </div>
                                        <button className='text-btn upload-applicant-img-btn' htmlFor="upload-applicant-img-btn">
                                            <input type="file" name="upload-applicant-img-btn" />
                                            Upload Photo
                                        </button>
                                    </div>
                                </div>
                                <div class="row">
                                    <div class="col-sm-6">
                                        <div class="form-group">
                                            <label htmlFor="name" class="form-label">Name</label>
                                            <input type="text" class="form-control" id="name" placeholder="Name" />
                                        </div>
                                    </div>
                                    <div class="col-sm-6">
                                        <div class="form-group">
                                            <label htmlFor="email" class="form-label">Email</label>
                                            <input type="email" class="form-control" id="email" placeholder="Email" />
                                        </div>
                                    </div>
                                </div>
                                <div class="row">
                                    <div class="col-sm-6">
                                        <div class="form-group">
                                            <label htmlFor="mobile_number" class="form-label">Mobile Number</label>
                                            <input type="text" class="form-control" id="mobile_number" placeholder="Mobile No." />
                                        </div>
                                    </div>
                                    <div class="col-sm-6">
                                        <div class="form-group">
                                            <label htmlFor="city" class="form-label">City</label>
                                            <input type="text" class="form-control" id="city" placeholder="City" />
                                        </div>
                                    </div>
                                </div>
                                <div className='row'>
                                    <div className='col-xl-3 col-lg-4 col-sm-6'>
                                        <button className='button button-blue w-100'>Next</button>
                                    </div>
                                </div>
                                <button className='text-btn save-exit-btn'>Save and Exit</button>
                            </div>
                        </div>
                    </div>

                </div>
            </section>
        </div>
    )
}

export default PersonalInformation;